import React, { Component, Fragment } from "react";
import PropTypes from "prop-types";
import { connect } from "react-redux";
import { withStyles, Typography } from "@material-ui/core";
import DeactivatedMessage from "../Messages/DeactivatedMessage";

const style = {
  wrapper: {
    padding: 15,
    boxSizing: "border-box",
    width: "100%",
    height: "100vh"
  }
};

class Shop extends Component {
  static propTypes = {
    isActivated: PropTypes.bool.isRequired,
    name: PropTypes.string.isRequired
  };

  render() {
    const { isActivated, name } = this.props;
    return (
      <Fragment>
        {!isActivated && <DeactivatedMessage />}
        <Typography variant="h5" align="left" style={style.wrapper}>
          Welcome, {name}
        </Typography>
      </Fragment>
    );
  }
}

const mapStateToProps = state => {
  return {
    isActivated: !!state.user.username,
    name: localStorage.getItem("username")
  };
};

export default connect(mapStateToProps)(withStyles(style)(Shop));
